<template>
  <div class="hello">
    <el-container>
          <el-header>
              <el-row>
                  <el-col>
                      <el-card>
                          <el-row type="flex" justify="center">
                              <el-col>
                                  <p>我们心里有野兽，我们有不可遏制的疯长的草和心魔。——雪小禅</p>
                              </el-col>
                          </el-row>
                      </el-card>
                  </el-col>
              </el-row>
          </el-header>
          <el-main>
              <el-row type="flex" justify="center">
                  <el-col :span="20">
                      <el-timeline>
                          <el-timeline-item
                          v-for="(activity, index) in activities"
                          :key="index"
                          :placement="activity.placement"
                          :type="activity.type"
                          :size="activity.size"
                          :timestamp="activity.timestamp">
                              <el-card>
                                  <p>【{{index}}】{{activity.content}}</p>
                              </el-card>
                          </el-timeline-item>
                      </el-timeline>
                  </el-col>
              </el-row>
          </el-main>
          <el-footer>
              <el-row>
                  <el-col>
                      <el-card>
                          <el-row type="flex" justify="center">
                              <el-col>
                                  <p>微博：<a href="https://weibo.com/p/1005055804887383/home?from=page_100505&mod=TAB&is_all=1#place">可薇-梳碧湖砍柴人</a>
                                  <br/>感谢来到本站阅读的每一位同学。<br/>如果同学们有什么启发或者感想都可以通过我的微博给我留言。再次感谢大家的阅读，祝大家开心快乐。</p>
                              </el-col>
                          </el-row>
                      </el-card>
                  </el-col>
              </el-row>
          </el-footer>
      </el-container>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      activities: [{
          content: '想创作一首属于自己曲子。音乐真是一种很神奇的东西。似乎它能知道你所有的情绪。',
          timestamp: '2019/10/04',
          placement: 'top',
          size: 'large',
          type: 'primary'
      },{
          content: '我想起那个炎日的夏天，在沙发躺着坐着在临风的窗口看着，余光中先生的散文和诗歌。那时我感受到了雪栅栏花带给我的清凉。',
          timestamp: '2019/09/29',
          placement: 'top',
          size: 'large',
          type: 'primary'
      },{
          content: '初秋，风旋转在牵牛花紫色的瓣上。听蝉，在说什么呢。都秋天了呀，只有窗户在吱吱地应答。',
          timestamp: '2019/09/21',
          placement: 'top',
          size: 'large',
          type: 'primary'
      },{
          content: '我推开窗时，风从方形的格子进来。兰花的叶子便簇拥着鞠着。',
          timestamp: '2019/09/14',
          placement: 'top',
          size: 'large',
          type: 'primary'
      },{
          content: '桃月兴桃桐月蚕，三月的米里带着枫木叶的香，那条小清溪，噢，那蘸着溪水的桑椹。',
          timestamp: '2019/04/13',
          placement: 'top',
          size: 'large',
          type: 'primary'
      },{
          content: '我看见一个把鞋子摆得很整齐的人,像心爱的小花开得很整齐的花瓣。',
          timestamp: '2019/04/13',
          placement: 'top',
          size: 'large',
          type: 'primary'
      }]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-container{
      background-image: url(../assets/el-container-bg.png);
  }
  .el-header{
      margin-bottom: 10%;
  }
  .el-header .el-card{
      background-color: #f9d5bb;
  }
  .el-header p{
      font-size: 18sp;
      color: #4c4c4c;
      text-align: center;
      padding: 2% 0;
  }
  .el-main .el-card{
      background-color: #dfcdc3;
      height: auto;
  }
  .el-main .el-card p{
      font-family: sans-serif;
      font-size: 0.55rem;
      color: #4c4c4c;
      text-align: center;
  }
  .el-footer .el-card{
      background-color: #f9d5bb;
  }
  .el-footer p{
      font-size: 18sp;
      color: #4c4c4c;
      text-align: center;
  }
</style>



